.sidebar {
    border-right: 0.5px solid rgb(230, 227, 227);
    flex: 1;
    min-height: 100vh;
    background-color: #fff;

    .top {
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;

        .logo {
            font-weight: bold;
            font: purple;
            font-size: 20px;
        }

        hr {
            height: 0;
            border: 0.5px solid rgb(230, 227, 227);
        }

    }

    .center {
        // padding-left: 10px;

        ul {
            list-style: none;
            margin: 0;
            padding: 0;

            .title {
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
            }

            li {
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover {
                    background-color: #ece8ff;
                }

                .icon {
                    font-size: 18px;
                    color: #7451f8
                }

                span {
                    font-size: 13px;
                    font-weight: 600;
                    margin-left: 10px;
                    color: gray;
                }
            }
        }
    }
    .bottomSidebar{
        display: flex;
        align-items: center;
        margin: 10px;
        .colorOptions{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8;
            cursor: pointer;
            margin: 5px;

            &:nth-child(1){
                background-color: whitesmoke;
            }
            &:nth-child(2){
                background-color: #333;
            }
        }
    }
}